.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.contact-list {
  position: relative;
}
.cnt-inquiry {
  height: 25.33vw;
  ul {
    width: 100%;
    max-width: 750px;
    background: #fff;
    li {
      float: left;
      width: 33.33%;
      text-align: center;
      margin: 6.3vw 0;
      strong {
        display: block;
        font-size: 5.6vw;
        color: #1f1f1f;
        font-weight: bold;
      }
      span {
        display: inline-block;
        margin: 1.3vw 0;
        font-size: 3.46vw;
        color: #898989;
      }
    }
  }
  .fixed {
    position: fixed;
    top: 14.66vw;
  }
}
.to-one {
  padding-top: 4.8vw;
  background: #f3f4f7;
  ul li {
    padding-bottom: 2.2vw;
    .ques {
      width: 91%;
      margin: 0 auto;
      padding: 4.6vw;
      background: #fff;
      box-sizing: border-box;
    }
    .working  {
      pointer-events: none;
      cursor: default;
    }
    .top-line {
      width: 100%;
      height: 2px;
      margin: 4.6vw 0;
      background: #e1e1e1;
    }
    .state {
      float: right;
      margin-top: .66vw;
      font-size: 3.2vw;
      font-weight: 700;
      color: #1f1f1f;
    }
    .ques.working .state {
      color: #d4d4d4;
    }
    .txt {
      @include long-text;
      margin-bottom: 2vw;
      font-size: 3.7vw;
      color: #000;
      line-height: 5.4vw;
      overflow: hidden;
    }
    .que-bt {
      font-size: 3.2vw;
      color: #aaa;
    }
    .que-bt.hv-ar::after {
      content: '';
      float: right;
      width: 2.566vw;
      height: 4vw;
      max-width: 17px;
      max-height: 17px;
      color: #ccc;
      background: url('/_ui/img/m/icn/icn-arrow02.png') no-repeat center;
      background-size: contain;
    }
    .ques a.on .que-bt.hv-ar::after {
      transform: rotate(180deg);
    }
    .answer {
      display: none;
      width: 91%;
      margin: 0 auto;
      padding: 0 4.6vw 4.6vw;
      background: #fff;
      box-sizing: border-box;
      .ANSWEND,
      .ANSWADD,
      .ANSWTMP {
        .state {
          float: left;
        }
      }
      .answerContents {
        width: 100%;
        padding: 15px 0 0;
      }
      .questionDetail {
        color: #898989;
      }
      .product-info {
        margin: 3.5vw 0;
        height: 20vw;
        padding: 13px;
        border: 1px solid #f3f4f7;
        overflow: hidden;
        img {
          float: left;
          width: 20%;
          height: 100%;
          object-fit: contain;
        }
        .info {
          float: left;
          width: 75%;
          margin-left: 5%;
          line-height: 6vw;
          .brand {
            float: left;
            font-weight: 900;
            font-size: 3.1vw;
          }
          .code {
            float: right;
            font-size: 3.1vw;
            color: #898989;
          }
          .subject {
            float: left;
            margin-top: 1vw;
            margin-bottom: 1vw;
            font-size: 3.7vw;
            height: 6vw;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .price {
            float: left;
            font-size: 3.3vw;
          }
          .ex-price {
            color: #898989;
            text-decoration: line-through;
            margin-right: 3px;
          }
        }
      }
      .file-name {
        font-size: 3.7vw;
        font-weight: 700;
        text-decoration: underline;
        color: #666;
      }
    }
  }
}
.use-info {
  padding: 9.33vw 4.8vw;
  background: #fff;
  h4 {
    font-size: 3.5vw;
    font-weight: 700;
    color: #1f1f1f;
  }
  ul {
    padding-top: 2.93vw;
  }
  li {
    font-size: 3.2vw;
    color: #aaa;
    line-height: 5.2vw;
    &::before {
      content: '·';
      margin-right: 6px;
      font-weight: 700;
    }
  }
  .indent {
    display: inline-block;
    text-indent: 2vw;
  }
}
.btn-oto {
  position: fixed;
  width: 100%;
  max-width: 750px;
  bottom: 0;
  background: #fff;
  text-align: center;
  a {
    display: block;
    width: 100%;
    padding: 6vw 0;
    font-size: 4.2vw;
    font-weight: 700;
    color: #1f1f1f;
  }
}
.add-form {
  .pop-close {
    display: block;
    width: 3.733vw;
    height: 3.733vw;
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    font-size: 0;
    text-indent: -999px;
    position: absolute;
    top: 5.333vw;
    right: 4.8vw;
    background-size: 100%;
  }
  h1 {
    line-height: 14.4vw;
    text-align: center;
    font-size: 4.533vw;
    color: #000;
    font-weight: 700;
  }
  form {
    margin-top: 10vw;
    padding: 0 7.466vw;
    ul {
      padding-bottom: 6.666vw;
      li {
        line-height: 10.8vw;
        border-bottom: .266vw #eee solid;
        padding-top: 3.733vw;
        select {
          background: #fff url('/_ui/img/m/icn/icn-arrow02.png') no-repeat 99% 50%;
          background-size: 3vw;
          font-size: 3.7vw;
          line-height: 10.8vw;
        }
        .slt-prdt {
          display: block;
          border: .266vw #eee solid;
          width: 100%;
          height: 21.333vw;
          box-sizing: border-box;
          color: #898989;
          font-size: 3.466vw;
          line-height: 21.333vw;
          text-align: center;
        }
        input {
          font-size: 3.73vw;
        }
        &:first-child {
          padding-top: 0;
          border-top: 0;
        }
        &.product-type {
          border: 0;
        }
        .slt-prdt-box {
          border: .266vw #eee solid;
          margin-top: 4.266vw;
          overflow: hidden;
          padding: 2.666vw;
          position: relative;
          .prod {
            float: left;
            position: relative;
            width: 12.8vw;
            margin-right: 2.666vw;
            &::before {
              content: '';
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              background: #000;
              opacity: .03;
              z-index: 1;
            }
            img {
              width: 100%;
            }
          }
          .info {
            margin-left: 15.466vw;
            .base-info {
              overflow: hidden;
              line-height: 2.66vw;
              .brand {
                float: left;
                font-family: 'helvetica';
                font-size: 2.666vw;
                font-weight: 900;
                color: #1f1f1f;
              }
              .product-code {
                float: right;
                font-family: 'helvetica';
                font-size: 2.4vw;
                color: #bbb;
              }
            }
            .t1 {
              @include long-text;
              font-size: 3.466vw;
              color: #1f1f1f;
              line-height: 4vw;
              margin-top: 2.266vw;
            }
            .btm {
              margin-top: 4vw;
              font-size: 3.2vw;
              line-height: 3.2vw;
              .discount {
                color: #9ba4b0;
                text-decoration: line-through;
                margin-right: 1.333vw;
              }
              .price {
                color: #1f1f1f;
                font-weight: 700;
              }
              p {
                float: right;
                .slt-btn {
                  font-size: 3.466vw;
                  color: #1f1f1f;
                  & + a {
                    &::before {
                      content: '';
                      display: inline-block;
                      width: .266vw;
                      height: 2.933vw;
                      margin: 0 2.666vw;
                      background: #eee;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    dl {
      border-top: .8vw #1f1f1f solid;
      padding-top: 3.733vw;
      font-size: 3.733vw;
      dt {
        input {
          display: block;
          width: 100%;
          height: 10.8vw;
          line-height: 10.8vw;
          border-bottom: .266vw #eee solid;
          color: #1f1f1f;
          font-size: 3.733vw;
        }
      }
      dd {
        border-bottom: .266vw #eee solid;
        textarea {
          resize: none;
          margin: 6vw 0;
          color: #1f1f1f;
          border: 0;
          display: block;
          width: 100%;
          height: 27.2vw;
          font-size: 3.733vw;
        }
        input {
          display: block;
          width: 100%;
          height: 10.8vw;
          line-height: 10.8vw;
          font-size: 3.733vw;
        }
        &.file {
          position: relative;
          height: 18.133vw;
          margin-bottom: 3.733vw;
          // input {
          //   width: 0;
          //   height: 0;
          //   font-size: 0;
          //   line-height: 0;
          //   overflow: hidden;
          //   visibility: hidden;
          //   position: absolute;
          // }
          .add-file {
            display: block;
            position: absolute;
            top: 3.2vw;
            left: 0;
            overflow: hidden;
            width: 100%;
            img {
              width: 11.733vw;
              height: 11.733vw;
              float: left;
              margin-right: 4.133vw;
            }
            span {
              float: left;
              display: block;
              width: 100%;
              margin-top: 1.2vw;
              font-size: 3.733vw;
              color: #898989;
              text-align: center;
            }
            span + span {
              font-size: 3.2vw;
              margin-top: 1.066vw;
              color: #aaa;
            }
            .in-tit {
              display: none;
              color: #ccc;
              font-size: 3.2vw;
              position: absolute;
              right: 0;
              bottom: 0;
              text-align: right;
            }
          }
          .seleted-file {
            padding: 7.066vw 4.8vw;
            > * {
              color: #1f1f1f;
            }
            .file-name {
              overflow: hidden;
              display: inline-block;
              width: 70%;
              white-space: nowrap;
              text-overflow: ellipsis;
              a {
                text-decoration: underline;
              }
            }
            .btns {
              float: right;
              .btn-select {
                &::after {
                  content: '';
                  display: inline-block;
                  width: .266vw;
                  height: 2.933vw;
                  margin: 0 2.666vw;
                  background: #eee;
                  vertical-align: middle;
                }
              }
            }
          }
        }
        &.sns-chk {
          position: relative;
          padding-top: 5.2vw;
          border-bottom: 0;
          margin-bottom: 9.066vw;
          .check {
            display: inline-block;
            input {
              width: 0;
              height: 0;
              font-size: 0;
              line-height: 0;
              overflow: hidden;
              visibility: hidden;
              position: absolute;
            }
            input + label {
              display: inline-block;
              width: 4.5vw;
              height: 4.5vw;
              text-align: center;
              vertical-align: middle;
              // background: url('/_ui/img/m/icn/icn-check01.png') no-repeat center;
              background-size: 3vw;
              border: .266vw #aaa solid;
            }
            input:checked + label {
              background: url('/_ui/img/m/icn/icn-check03.png') no-repeat center;
              background-size: 3vw;
              border-color: #1f1f1f;
            }
            input + label + label {
              font-size: 3.733vw;
              color: #898989;
              display: inline-block;
            }
            input:checked + label + label {
              color: #1f1f1f;
            }
          }
        }
      }
    }
  }
  .notice {
    padding: 7.333vw 11.466vw 16.666vw;
    li {
      list-style: disc;
      font-size: 2.933vw;
      color: #aaa;
      line-height: 4vw;
      margin-top: 1.733vw;
      a {
        color: #1f1f1f;
      }
      &:first-child {
        margin-top: 0;
      }
    }
  }
  .in-txt {
    position: relative;
    input {
      width: 100%;
      font-size: 3.733vw;
    }
    input + span {
      position: absolute;
      right: 0;
      top: 0;
      display: none;
      color: #ccc;
      font-size: 3.2vw;
      line-height: 10.8vw;
    }
  }
  .in-txtarea {
    position: relative;
    textarea + span {
      position: absolute;
      right: 0;
      bottom: 3.733vw;
      display: none;
      color: #ccc;
      font-size: 3.2vw;
    }
  }
}
.search-prod {
  .pop-close {
    display: block;
    width: 3.733vw;
    height: 3.733vw;
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    font-size: 0;
    text-indent: -999px;
    position: absolute;
    top: 5.333vw;
    right: 4.8vw;
    background-size: 100%;
  }
  h1 {
    line-height: 14.4vw;
    text-align: center;
    font-size: 4.533vw;
    color: #000;
    font-weight: 700;
  }
  .prod-more {
    display: block;
    width: 7.733vw;
    height: 7.733vw;
    background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
    font-size: 0;
    text-indent: -999px;
    margin: 12vw auto 16.533vw;
    background-size: 100%;
  }
}
.search-prod > .tab {
  display: none;
}
.search-prod > .tab.tab0 {
  display: block;
}
.search-prod .prod-list {
  min-height: 100vw;
  li {
    border-bottom: .266vw #f7f7f7 solid;
    overflow: hidden;
    padding: 4.8vw;
    box-sizing: border-box;
    &:first-child {
      padding-top: 0;
    }
    .t1 {
      margin-top: 2.533vw;
      font-size: 3.466vw;
      color: #1f1f1f;
      margin-bottom: 2vw;
    }
    .t2 {
      font-size: 2.933vw;
      color: #aaa;
      margin-bottom: 4vw;
    }
    .t3 {
      margin: 1.333vw 0 2.666vw;
    }
    .t4 {
      font-size: 3.733vw;
      height: 8.666vw;
      margin-bottom: 1.333vw;
      color: #1f1f1f;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: left;
      line-height: 1.2;
      white-space: nowrap;
      word-wrap: break-word;
      // display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      box-sizing: border-box;
      word-break: normal;
      -moz-line-clamp: 2;
      -moz-box-orient: vertical;
      display: -moz-box;
      clear: both;
      text-overflow: ellipsis;
    }
    .prod {
      float: left;
      width: 25.333vw;
      img {
        display: block;
        width: 100%;
      }
    }
    .info {
      float: left;
      width: 61.066vw;
      margin-left: 4vw;
      position: relative;
      height: 30.933vw;
      .brand {
        float: left;
        font-size: 2.666vw;
        font-weight: 700;
        color: #1f1f1f;
      }
      .brand + span {
        float: right;
        font-size: 2.4vw;
        color: #bbb;
      }
      .select {
        font-size: 3.2vw;
        color: #1f1f1f;
        border: .266vw #f1f1f1 solid;
        height: 7.2vw;
        line-height: 7.2vw;
        padding: 0 2.666vw;
        display: inline-block;
        position: absolute;
        bottom: 0;
        right: 0;
      }
    }
    dl {
      overflow: hidden;
      color: #898989;
      line-height: 5.333vw;
      position: absolute;
      bottom: 0;
      width: 100%;
      dt {
        float: left;
        clear: both;
        width: 17.333vw;
        font-size: 3.2vw;
      }
      dd {
        float: left;
        font-size: 3.2vw;
      }
      .color {
        font-size: 3.466vw;
        color: #1f1f1f;
      }
    }
  }
}
.btn-contact {
  display: block;
  position: fixed;
  bottom: 0;
  width: 100%;
  max-width: 750px;
  height: 16vw;
  font-size: 4.266vw;
  color: #1f1f1f;
  text-align: center;
  line-height: 16vw;
  background: #fff;
  border-top: .266vw solid #f1f1f1;
  z-index: $icn-navi-zindex;
}
.contact-list ~ footer {
  padding-bottom: 26.66vw;
}
.cs-depth3 {
  ul {
    margin-bottom: 4.8vw;
  }
}
.select-period {
  margin-bottom: 7.066vw;
  padding: 0 4.8vw;
  ul {
    overflow: hidden;
    width: 100%;
    border: .266vw solid #f1f1f1;
    li {
      a {
        float: left;
        display: block;
        width: 33.33%;
