        text-align: center;
        font-size: 3.466vw;
        color: #ccc;
        box-sizing: border-box;
        border-left: .266vw solid #f1f1f1;
      }
      &:first-child {
        a {
          border: 0;
        }
      }
      &.on {
        a {
          background: #f8f8f8;
          color: #1f1f1f;
        }
      }
    }
  }
}
.qnaImgDetail {
  width: 100%;
}
@media (min-width: 750px) {
  .scr-navi {
    width: 678px;
