          margin: 26.25px 0;
          height: 150px;
          .info {
            line-height: 45px;
            .brand,
            .code {
              font-size: 23px;
            }
            .subject {
              margin-top: 7.5px;
              margin-bottom: 7.5px;
              font-size: 28px;
              height: 45px;
            }
            .price {
              font-size: 25px;
            }
          }
        }
        .file-name {
          font-size: 27.75px;
        }
      }
    }
  }
  .use-info {
    padding: 70px 36px;
    h4 {
      font-size: 26px;
    }
    ul {
      padding-top: 22px;
      li {
        font-size: 24px;
        line-height: 36px;
      }
    }
    .indent {
      text-indent: 12px;
    }
  }
  .btn-oto a {
    padding: 50px 0;
    font-size: 32px;
  }
