      font-size: 34px;
    }
    form {
      margin-top: 75px;
      padding: 0 56px;
      ul {
        padding-bottom: 50px;
        li {
          line-height: 81px;
          border-bottom: 2px #eee solid;
          padding-top: 28px;
          select {
            background: #fff url('/_ui/img/m/icn/icn-arrow02.png') no-repeat 99% 50%;
            background-size: 22.5px;
            font-size: 28px;
            line-height: 81px;
          }
          .slt-prdt {
            border: 2px #eee solid;
            height: 160px;
            font-size: 26px;
            line-height: 160px;
          }
          input {
            font-size: 28px;
          }
          .slt-prdt-box {
            border: 2px #eee solid;
            margin-top: 32px;
            padding: 20px;
            .prod {
              width: 96px;
              margin-right: 20px;
              img {
                width: 100%;
              }
            }
            .info {
              margin-left: 116px;
              .base-info {
                line-height: 20px;
                .brand {
                  font-size: 20px;
                }
                .product-code {
                  font-size: 18px;
                }
              }
              .t1 {
                font-size: 26px;
